@import "color.scss";


.header {
  position: relative;

  &__bg {
    position: absolute;
    width: 100%;
    height: 100vh;

    &:after {
      content: "";
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 5;
      background-color: rgba(0, 0, 0, 0.5);
    }

  }
  &__video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
    object-fit: cover;
    //@media screen and (max-width: 767px) {
    //  height: 90%;
    //}
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    object-fit: cover;
    //@media screen and (max-width: 767px) {
    //  height: 90%;
    //}
  }

  &__content {
    position: relative;
    z-index: 100;
    top: 0;
    left: 0;
    padding-top: 20px;

  }

  &__logo {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 200px;
    border-radius: 50%;

    @media screen and (max-width: 767px) {
      width: 150px;
      height: 150px;
    }

    &-image {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }

    &:before {
      content: '';
      position: absolute;
      display: block;
      width: 101%;
      height: 101%;
      border-radius: 50%;
      z-index: -10;
      background: linear-gradient(60deg, #fddb00, #fd8e17, #ef4e7b, #a166ab, #5073b8, #1098ad, #07b39b, #6fba82);
      animation: animate 2s linear infinite;

      @keyframes animate {
        0% {
          transform: rotate(0deg);
        }
        50% {
          transform: rotate(180deg);
        }
        100% {
          transform: rotate(360deg);
        }
      }

    }
    @media screen and (max-width: 1199px) {
      position: relative;
    }
  }

  &__nav {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 70px;
    @media screen and (max-width: 1199px) {
      position: absolute;
      flex-direction: column;
      align-items: flex-start;
      overflow: hidden;
      right: 20px;
      top: 10px;
      width: 250px;
      height: 40px;
      padding: 0;
      border-radius: 20px;
      background: rgba(255, 255, 255, 0.5);
      transition: .3s;
      &:after {
        content: "";
        position: absolute;
        height: 100%;
        border: 1px solid rgba(0, 55, 118, 0.3);
        right: 50px;
        @media screen and (max-width: 767px) {
          display: none;
        }
      }
    }

    @media screen and (max-width: 767px) {
      position: fixed;
      flex-direction: row;
      align-items: center;
      left: 0;
      top: auto;
      bottom: 0;
      width: 100%;
      height: 70px;
      border-radius: 0;
      background-color: $fixed-block;
    }
  }

  .active {
    height: 160px;
    .nav__btn {
      &:before {
        transform: translate(-50%) rotate(135deg);
      }
    }
    @media screen and (max-width: 767px) {
      height: 70px;
    }
  }

  .nav {

    &__item {
      position: relative;
      display: block;
      @media screen and (max-width: 1199px) {
        display: flex;
        align-items: center;
        width: 170px;
        margin: 5px 7px;
        &:hover {
          .icon_color {
            fill: $text-color;
            transition: .3s;
          }
          .nav__items {
            color: $text-color;
            transition: all .3s;
          }
        }
      }
      @media screen and (max-width: 767px) {
        justify-content: center;
        height: 100%;
        width: 100%;
        margin: 0;
        border-right: 1px solid rgba(0,55,118,.3);
      }
    }

    &__items {
      display: none;
      @media screen and (max-width: 1199px) {
        display: inline-block;
        width: 100%;
        text-align: center;
        font-size: 2rem;
        color: $body-text;
      }
      @media screen and (max-width: 767px) {
        display: none;
      }
    }

    &__hidden {
      @media screen and (max-width: 1199px) {
        display: none;
      }
    }
    &__btn {
      display: none;
      &:hover {
        &:before {
          border-bottom: 3px solid $btn-focus;
          border-left: 3px solid $btn-focus;
        }
      }
      @media screen and (max-width: 1199px) {
        display: block;
        width: 30px;
        height: 30px;
        position: absolute;
        top: 5px;
        right: 10px;
        cursor: pointer;
        &:before {
          content: "";
          width: 15px;
          height: 15px;
          border-radius: 2px;
          border-bottom: 3px solid $arrow-btn;
          border-left: 3px solid $arrow-btn;
          position: absolute;
          top: 42%;
          left: 50%;
          transform: translate(-50%,-50%) rotate(-45deg);
          transition: all .3s;
        }
      }
      @media screen and (max-width: 767px) {
        display: none;
      }
    }

    &__icon {
      transition: .3s;
      &:hover {
        transform: scale(1.2);
      }
      @media screen and (max-width: 1199px) {
        width: 30px;
        height: 30px;
        &:hover {
          transform: none;
        }
      }
      @media screen and (max-width: 767px) {
        width: 50px;
        height: 50px;
      }
      @media screen and (max-width: 425px) {
        width: 30px;
        height: 30px;
      }
    }
  }

  .promo {

    &__block {
      margin-top: 150px;
      margin-bottom: 70px;
      padding-left: 20px;
      @media screen and (max-width: 1199px) {
        margin-top: 50px;
        margin-bottom: 100px;
      }

      @media screen and (max-width: 767px) {
        margin-top: 30px;
        margin-bottom: 50px;
      }
    }

    &__title {
      font-weight: 700;
      font-size: 9.6rem;
      @media screen and (max-width: 767px) {
        font-size: 7rem;
      }
      @media screen and (max-width: 425px) {
        font-size: 4.6rem;
      }

    }

    &__text {
      padding-top: 20px;
      font-weight: 700;
      font-size: 3.6rem;
      @media screen and (max-width: 767px) {
        font-size: 2rem;
      }

      @media screen and (max-width: 425px) {
        font-size: 1.6rem;
      }
    }

  }

  &__btn {
    position: absolute;
    right: 100px;
    width: 350px;
    height: 80px;
    border-radius: 20px;
    background: linear-gradient(90deg, #0047FF 0%, #29D8FF 100%);
    color: $body-text;
    font-size: 2.4rem;
    transition: .5s;

    &:hover {
      background: linear-gradient(270deg, #0047FF 0%, #29D8FF 100%);
    }

    &:focus {
      background: $btn-focus;
    }

    @media screen and (max-width: 1199px) {
      width: 290px;
      height: 60px;
      right: 50%;
      transform: translate(50%);
    }
  }
}





